* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: $background-color;
    font-family: 'Geometria';
}

.container {
    max-width: 1480px;
    margin: 0 auto;
    padding: 0 20px;
}

.btn {
    cursor: pointer;
    &.btn-primary {
        background: $accent-color;
        border-radius: 8px;
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        text-align: center;
        color: $input-card-color;
        padding: 30px;
        min-width: 258px;
        &:hover {
            background-color: $accent-color-hover;
        }
    }

    &.btn-secondary {
        padding: 12px 16px;
        border: 1px solid $stroke-color;
        border-radius: 5px; 
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        text-align: center;
        color: $titles-color;
        background-color: transparent;
        &:hover {
            background: $accent-color;
            color: $input-card-color;
        }
    }
}

.basis-subtitle {
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    color: $sub-text;
    margin-bottom: 60px;
}
